<template>
  <div id="app">
    <router-view></router-view>
    <layer :class="{ active:showLayer }"></layer>
    <button type="button" name="button" class="btn" @click="toggleLayer">
      <i class="icon iconfont" :class="{'icon-all':!showLayer,'icon-close':showLayer}"></i>
    </button>


    <div class="blank" :class="{ active:black }"></div>




  </div>
</template>

<script>
import layer from '@/components/Layer'

export default {
  name: 'app',
  data(){
    return{
      showLayer: false,
      black: false,
    }
  },
  methods: {
    toggleLayer:function(){
      this.showLayer = !this.showLayer;
    }
  },
  components: { layer }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  width:100%;
  overflow: scroll;
}
a{
  text-decoration: none;
}
.btn{
    position: fixed;
    top: 30px;
    right: 15px;
    width: 40px;
    height: 40px;
    padding:0;
    border: 0;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 3px;
    outline: none;
    font-weight: bold;
}

.iconfont{
    color: #fff;
    height:100%;
    width:100%;
    font-size: 17px;
}

.blank{
  display: none;
  height:100%;
  width:100%;
  background: rgba(0,0,0,.5);
  position: absolute;
  top:0;
  left:0;
  z-index: 1000;
}
.blank.active{
  display: block;
}







</style>



