<template>
  <div class="layer">
    <header>
      <h1 class="logo">
        王安安的裤兜
        <!-- <img src="" alt=""> -->
      </h1>
    </header>

    <main>
      <ul class="list">
        <li v-for="(nav,index) in navs" :key="nav.title">
          <a href="#" class="" :style="'background-color:'+nav.bgcolor+';'" >{{ nav.title }}</a>
        </li>
      </ul>
    </main>

    <div class="me" :class="{ active:me }">
      <p>王安安</p>
    </div>

  </div>
</template>

<script>
import {navs} from '@/data'
import {ans} from '@/data'

export default {
  name: 'layer',
  data () {
    return {
      msg: '王安安',
      navs:navs,
      ans:ans,
      me: false,
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped lang="scss">
  .layer{
    transform: translate(0, -100%);
    transition: .6s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: rgba(255, 255, 255, .98);
    &.active{
      transform: translate(0, 0);
    }
  }
  .logo{
    padding: 15px;
  }
  .list{
    padding: 15px;
  }
  .list li{
    list-style: none;
    width: 32%;
    display: inline-block;
    text-align: center;
    border-left:4px solid rgba(0,0,0,0);
    border-bottom:4px solid rgba(0,0,0,0);
    a{
      height:100%;
      width:100%;
      display: block;
      height: 80px;
      line-height: 80px;
      color: #fff;
      font-weight: bold;
      border-radius: 3px;
    }
  }

  .me{
    height:100%;
    width:100%;
    background: rgba(0,0,0,.8);
    position: absolute;
    top:0;
    left:0;
    color:#fff;
    transform: translate(0, -100%);
    transition: .6s;
    &active{
      transform: translate(0, 0);
    }
  }


</style>



